import React from 'react'

const ReplacementMrfOffcanvas = () => {
  return (
    <>
     <div class="offcanvas offcanvas-end" id="replacementMrfOffcanvas">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-5 fw-bold text-primary">Replacement MRF</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body">
            <div class="formPanel">
                <div class="form-group mb-3">
                    <label for="">Employee ID</label>
                    <select class="form-select">
                        <option value="">Please Select</option>
                    </select>
                </div>
                <div class="form-group mb-3">
                    <label for="">Employee Name</label>
                    <input type="text" class="form-control" value="" placeholder="Enter Name"/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Head of Department</label>
                    <input type="text" class="form-control" value="" placeholder="Please Enter"/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Requisition for Department*</label>
                    <input type="text" class="form-control" value="" placeholder="Requisition for Department..."/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Designation</label>
                    <input type="text" class="form-control" value="" placeholder="Please Enter"/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Priority*</label>
                    <select class="form-select">
                        <option value="">Please Select</option>
                    </select>
                </div>

            </div>
        </div>
        <div class="offcanvas-footer">
            <div class="d-flex align-items-center justify-content-end gap-3">
                <button type="button" class="btn btn-primary gradientBg">Save Replacement</button>
                <button type="button" class="btn btn-secondary">Reset</button>
            </div>
        </div>
    </div>
    </>
  )
}

export default ReplacementMrfOffcanvas