import React from 'react'

const CandidateDet = () => {
  return (
    <>
     <div class="offcanvas offcanvas-end" id="candidateDet">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-5 fw-bold text-primary">Candidate Details</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body">
            <div class="formPanel mrfMain">
                <div class="form-group mb-3">
                    <label for="">Candidate ID</label>
                    <input type="text" class="form-control" placeholder="QD9013" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Candidate Name</label>
                    <input type="text" class="form-control" placeholder="Vaibhav Saini" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Email</label>
                    <input type="text" class="form-control" placeholder="abc@gmail.com" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Mobile No.</label>
                    <input type="text" class="form-control" placeholder="9829190130" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Designation</label>
                    <input type="text" class="form-control" placeholder="Lorem Ipsum" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Experience</label>
                    <input type="text" class="form-control" placeholder="2" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">View CV</label>
                    <div class="position-relative">
                        <input type="text" class="form-control" placeholder="vaibhavSainiCV.pdf" disabled/>
                        <div class="cvDownload">
                            <a href="#"><img src="images/view-icon.svg" alt="" width="26" data-bs-toggle="tooltip"
                                    data-bs-placement="top" data-bs-title="View"/></a>
                            <a href="#"><img src="images/download-iconbg.svg" alt="" width="26" data-bs-toggle="tooltip"
                                    data-bs-placement="top" data-bs-title="Download"/></a>
                        </div>
                    </div>
                </div>
                <div class="form-group mb-3">
                    <label for="">OK For Interview </label>
                    <input type="text" class="form-control" placeholder="Yes" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="">Not OK For Interview </label>
                    <input type="text" class="form-control" placeholder="-" disabled/>
                </div>
                <div class="form-group mb-3">
                    <label for="" class="d-block">Justification for Vacancy</label>
                    <textarea name="" id="" class="w-100" placeholder="Please Enter here" disabled
                        style={{height: "60px !important" }}></textarea>
                </div>
            </div>
        </div>
        <div class="offcanvas-footer">
            <div class="d-flex align-items-center justify-content-end gap-3">
                <a type="button" class="btn btn-secondary">Back</a>
            </div>
        </div>
    </div>
    
    
    </>
  )
}

export default CandidateDet