import React from 'react'
import Lottie from 'react-lottie-player'
import success from "../../../images/success.json"

const ApprovelmsgModal = () => {
  return (
    <>
         <div class="modal fade errorMsg" id="approvelmsg" data-bs-backdrop="static">
        <div class="modal-dialog">
            <div class="modal-content border-0 rounded-1">
                <div class="modal-header border-0 py-2">
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body px-3 pb-2">
                    <div class="d-flex">
                        <div>
                        <Lottie
                                            loop
                                            // src="../images/star-icon.json"
                                            animationData={success}
                                            background="transparent"
                                            speed="1"
                                            play
                                            style={{height: "80px", width: "80px"}}
                                            autoplay></Lottie>
                            {/* <lottie-player src="images/success.json" loop autoplay
                                style={{height: "80px", width: "80px"}}></lottie-player> */}
                        </div>
                        <div class="actionmdl ps-3">
                            <h3>Lorem Ipsum is simply </h3>
                            <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</p>
                            <a href="#" class="">Cancel</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </>
  )
}

export default ApprovelmsgModal
