import React from 'react'
import Button from '../../commanComponent/Button'
import NewHiringOffCanvas from '../../modal/hiring/NewHiringOffCanvas'
import ReplacementMrfOffcanvas from '../../modal/hiring/ReplacementMrfOffcanvas'
import Lottie from 'react-lottie-player'
import businesshiring from "../../../images/business-hiring.json"
import { CustomButton } from '../../globalComponents'
import {useNavigate } from 'react-router-dom'

const CreateMrf = () => {
    const Navigate =useNavigate()




    const createMrfProcess =()=>{
        Navigate('/mrf-process')
    }
    return (
        <>
            <div class="cardBox mb-3 createNewMrf">
                <div class="row align-items-center">
                    <div class="col-md-9 col-xl-10">
                        <div class="row align-items-center">
                            <div class="col-md-3">
                            <Lottie
                                            loop
                                            // src="../images/star-icon.json"
                                            animationData={businesshiring}
                                            background="transparent"
                                            speed="1"
                                            play
                                            style={{width: "100%", height: "124px"}}
                                            autoplay></Lottie>
                                {/* <lottie-player src="images/business-hiring.json" background="transparent" speed="1"
                                    style={{width: "100%", height: "124px"}} loop autoplay></lottie-player> */}
                            </div>
                            <div class="col-md-9">
                                <div class="cardHead pb-0">
                                    <h3>Create <span>New MRF</span></h3>
                                    <p class="mb-0">Lorem Ipsum is simply dummy text of the printing and typesetting
                                        industry. Lorem
                                        Ipsum has been the industry's</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-3 col-xl-2">
                    <Button 
                        btnClassName={"btn btn-outline-primary w-100 mb-2"} 
                        // dataBsToggle={"offcanvas"} 
                        // dataBsTarget={"#newHiringOffcanvas"}
                        onClickFun={()=> createMrfProcess()}
                    >New Hiring
                    </Button>
                    <Button btnClassName={"btn btn-primary gradientBg w-100"} 
                        dataBsToggle={"offcanvas"} 
                        dataBsTarget={"#replacementMrfOffcanvas"}>Replacement
                    </Button>

                    </div>
                </div>
            </div>
            <NewHiringOffCanvas/>
            <ReplacementMrfOffcanvas/>
        </>
    )
}

export default CreateMrf
