import React from 'react'
import Lottie from 'react-lottie-player'
import Backup from "../../../images/Backup.json"

const SureModal = () => {
  return (
    <div>
      <div class="modal fade attendanceViewodal" id="sureMdl">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content rounded-1 border-0">

                <div class="modal-body ">
                    <div class="text-center suremodalCon">
                        <div class="d-flex justify-content-center">
                        <Lottie
                                            loop
                                            animationData={Backup}
                                            background="transparent"
                                            speed="1"
                                            play
                                            style={{width: "90px", height: "90px"}}
                                            autoplay></Lottie>
                            {/* <lottie-player class="my-4" src="images/Backup.json" background="transparent"
                                style={{width: "90px", height: "90px"}} loop autoplay></lottie-player> */}
                        </div>
                        <h6 class="fw-semibold mb-2">Are You Sure?</h6>
                        <p class="mb-4 px-4">Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                        </p>
                        <a href="#approvelmsg" data-bs-toggle="modal"
                            class="btn btn-primary gradientBg w-100">Submit</a>
                        <a href="" class="btn btn-outlineBack mt-2">Back</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
  )
}

export default SureModal
