import React from 'react'

const StatusCandidate = () => {
  return (
    <>
    <div class="offcanvas offcanvas-end" id="statusCandidate">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-5 fw-bold text-primary">Candidate Details</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body">
            <div class="detailsCandidate">
                <ul class="detailsList">
                    <li class="select active">
                        <figure class="m-0">
                            <img src="images/greenRight.svg" alt="" width="45"/>
                        </figure>
                        <strong>MRF raised <b>24/04/2024</b></strong>
                    </li>
                    <li class="select ">
                        <figure class="m-0">
                            <img src="images/greenRight.svg" alt="" width="45"/>
                        </figure>
                        <strong>Approved by manager <b>Date -</b></strong>
                    </li>
                    <li class="select">
                        <figure class="m-0">
                            <img src="images/greenRight.svg" alt="" width="45"/>
                        </figure>
                        <strong>Approved by HR manager <b>Date -</b></strong>
                    </li>
                    <li class="select ">
                        <figure class="m-0">
                            <img src="images/greenRight.svg" alt="" width="45"/>
                        </figure>
                        <strong>Assign to recruiter <b>Date -</b></strong>
                    </li>
                    <li class="select nonactive">
                        <figure class="m-0">
                            <img src="images/redCross.svg" alt="" width="45"/>
                        </figure>
                        <strong>Interview ongoing <b>Date -</b></strong>
                    </li>
                    <li class="select ">
                        <figure class="m-0">
                            <img src="images/redCross.svg" alt="" width="45"/>
                        </figure>
                        <strong>Candidate Selected <b>Date -</b></strong>
                    </li>
                    <li class="select ">
                        <figure class="m-0">
                            <img src="images/redCross.svg" alt="" width="45"/>
                        </figure>
                        <strong>Final <b>Date -</b></strong>
                    </li>
                </ul>
            </div>
        </div>
        <div class="offcanvas-footer">
            <div class="d-flex align-items-center justify-content-end gap-3">
                <a type="button" class="btn btn-secondary">Back</a>
            </div>
        </div>
    </div>
    
    
    </>
  )
}

export default StatusCandidate