import React from 'react'

const MrfApprovalModal = () => {
  return (
    <>
    <div class="modal fade correctionAprovel" id="mrfApproval">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content rounded-0 border-0">
                <div class="modal-header border-0">
                    <h1 class="modal-title fs-5 fw-bold text-primary">MRF Approval</h1>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body pt-0">
                    <div class="d-flex align-items-center justify-content-between mb-4">
                        <strong class="fs-7 text-light">MRF ID<span
                                class="text-primary fw-bolder d-block">565</span></strong>
                        <strong class="fs-7 text-light text-end">Applied For<span
                                class="text-primary fw-bolder d-block">Vacancy</span></strong>
                    </div>
                    <span class="text-primary fw-semibold mb-2 d-block fs-8">Enter Reason</span>
                    <div class="form-group mb-3">
                        <textarea class="form-control" placeholder="Enter Reason" style={{height: "70px"}}></textarea>
                    </div>
                    <div class="my-3 d-flex justify-content-center gap-3">
                        <a href="#" class="btn-danger px-4">Reject </a>
                        <a href="#" class="btn-success">Approve </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
      
    </>
  )
}

export default MrfApprovalModal
