import React from 'react'
import {CreateMrf,MrfProceses,MrfApprovalStatus,MrfSubmissionStatus,CompleteMrfSteps} from "../components/hiring/mrf"
const Mrf = () => {
  return (
    <>
       
        <MrfProceses />
        <CreateMrf />

        <div class="cardBox myRequest mb-3">
            <div class="cardHead">
                <img src="images/myrequest-icon.svg" alt="" width="35"/>
                <h3 class="ms-2">MRF Status</h3>
            </div>
            <ul class="nav nav-tabs leaveTabs mb-4">
                <li class="nav-item">
                    <button class="nav-link active" data-bs-toggle="tab" data-bs-target="#mrfSubmissionTb"
                        type="button">MRF Submission</button>
                </li>
                <li class="nav-item">
                    <button class="nav-link" data-bs-toggle="tab" data-bs-target="#mrfApprovalTb" type="button">MRF
                        Approval</button>
                </li>
            </ul>
            <div class="tab-content">
                <MrfSubmissionStatus/>
                <MrfApprovalStatus/>
            </div>
        </div>
        <CompleteMrfSteps />


    </>
  )
}

export default Mrf
